:root{
  --text-primary:#000;
  --bgPrimary:#fff;
  --bgSecondary: #F9F9F9;
  --pink: #0071bd;
  --light-blue: #e6f7ff;
  --border: #d9d9d9;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

iframe{
  display: none;
}

a:hover{
  color: #0071bd !important;
  color: var(--pink) !important;
}

.app{
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.navbar{
  background-color: rgb(0, 21, 41);
}

.main{
  flex: 1 1;
  width: 100%;
}

.routes{
  padding: 20px;
}

.nav-container{
  left: 0;
  margin: 10px;
  margin: 0px;
  background-color: rgb(0, 21, 41);
}

.logo-container{
  background-color: #001529;
  display: flex;
  padding: 20px;
  align-items: center;
  width: 100%;
}

.logo{
  margin:0 0 0 15px;
}

.logo a{
  color: white;
}

.menu-control-container{
  display: none !important;
  position: absolute !important;
  right: 10px !important;
  top: 25px !important;
  font-size: 1.2rem !important;
  background-color: #F9F9F9 !important;
  background-color: var(--bgSecondary) !important;
  border: none !important;
}

.loader{
  height: 81vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width:1300px){
  .main{
    margin-left: 50px;
  }
}

@media screen and (max-width:1170px){
  .main{
    margin-left: 50px;
  }
}

@media screen and (max-width:1000px){
  .main{
    margin-left: 100px;
  }
}

@media screen and (max-width:800px){
  .app{
    flex-direction: column;
    overflow: hidden;
  }

  .navbar{
    flex: 1 1;
  }

  .main{
    flex: 1 1;
    margin-top: 90px;
    margin-left: 0px;
    margin-right: 10px;
  }

  .nav-container{
    height: 8vh;
    position:fixed;
    width: 100%;
    z-index: 100;
    background-color: #F9F9F9;
    background-color: var(--bgSecondary);
  }

  .menu-control-container{
    display: block !important;
  }

  .ant-menu{
    position: absolute;
    right: 0px;
  }

  .home-title{
    font-size: 1.4rem !important;
  }

  .show-more{
    font-size: 1.3rem !important;
  }
}

.coin-detail-container{
  margin: 30px;
}

.coin-heading-container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-bottom: 1px solid #d9d9d9;
  border-bottom: 1px solid var(--border);
  padding-top: 20px;
  padding-bottom: 20px;
  grid-gap: 10px;
  gap: 10px;
}

.coin-heading-container .coin-name{
  font-weight: 900;
  color: #0071bd;
  color: var(--pink);
}

.coin-heading-container p{
  font-size: 1rem;
  opacity: 0.9;
  margin-bottom: 20px;
}

.stats-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 40px;
  gap: 40px;
}

.stats-container h2{
  font-weight: 700;
  font-size: 1.4rem;
  margin-top: 20px;
  color: #0071bd;
  color: var(--pink);
}

.coin-details-heading{
  font-weight: 700 !important;
  margin-top: 20px !important;
  color: #0071bd !important;
  color: var(--pink) !important;
}

.coin-stats{
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #d9d9d9;
  border-bottom: 1px solid var(--border);
